@tailwind base;
@tailwind components;
@tailwind utilities;

:root{
    --back-color : #5cdb95;
    --text-color : #05386b;
}

.parent{
    height: calc(100vh - 100px);
}

@layer components{
    .cardMainPage{
        box-shadow:0px 0px 15px rgb(0,0,0,0.32);
        @apply flex flex-col items-center justify-start text-2xl gap-5 p-2 py-3 rounded-md bg-white;
    }
    .Icons{
        @apply bg-[#aff0cc] p-2 w-fit h-fit rounded-full text-3xl md:text-2xl cursor-pointer hover:bg-white;
    }
    .HeaderIcons{
        @apply bg-[#aff0cc] p-2 w-fit h-fit text-xl rounded-sm hover:bg-white cursor-pointer;
    }
}

#left-side-menu{
    border-right: 1px solid #379683;
}